// ** React Imports
import React, {Fragment} from 'react'
// ** Custom Components
import Avatar from '@components/avatar'
// ** Third Party Components
import classnames from 'classnames'
import PerfectScrollbar from 'react-perfect-scrollbar'
import {Bell, AlertTriangle} from 'react-feather'
import {
    Button,
    Badge,
    Media,
    DropdownMenu,
    DropdownItem,
    DropdownToggle,
    UncontrolledDropdown
} from 'reactstrap'
import {Link, withRouter} from "react-router-dom";
import {connect} from "react-redux";
import {readNotification} from "../../../../redux/reducers/app-reducer";

const NotificationDropdown = (props) => {
    let cntNotif = 0;
    const notificationsArray = props.notifications.map((item, index) => {
        if (item.user_notifications_checked === '0') cntNotif++;
        return (
            {
                link: item.user_notifications_link,
                avatarIcon: <AlertTriangle size={14}/>,
                color: 'light-warning',
                subtitle: (<p>{item.user_notifications_text}</p>),
                title: (
                    <Media tag='p' heading>
                        {item.user_notifications_title}
                    </Media>
                ),
                userId: item.user_notifications_user_id,
                notifId: item.user_notifications_id,
                checked: item.user_notifications_checked
            }
        )
    });
    const readNotif = (e, userId, notifId, link) => {
        e.preventDefault();
        props.readNotification(userId, notifId);
        if (link !== '') {
            props.history.push(link);
        }
    }

    // ** Function to render Notifications
    /*eslint-disable */
    const renderNotificationItems = () => {
        return (
            <PerfectScrollbar
                component='li'
                className='media-list scrollable-container'
                options={{
                    wheelPropagation: false
                }}
            >
                {notificationsArray.map((item, index) => {
                    if (item !== '' && item.checked === '0') return (
                        <Link key={index} className='d-flex' to={''} onClick={(e) => readNotif(e, item.userId, item.notifId, item.link)}>
                            <Media
                                className={classnames('d-flex', {
                                    'align-items-start': !item.switch,
                                    'align-items-center': item.switch
                                })}
                            >
                                {!item.switch ? (
                                    <Fragment>
                                        <Media left>
                                            <Avatar
                                                {...(item.img
                                                    ? {img: item.img, imgHeight: 32, imgWidth: 32}
                                                    : item.avatarContent
                                                        ? {
                                                            content: item.avatarContent,
                                                            color: item.color
                                                        }
                                                        : item.avatarIcon
                                                            ? {
                                                                icon: item.avatarIcon,
                                                                color: item.color
                                                            }
                                                            : null)}
                                            />
                                        </Media>
                                        <Media body>
                                            {item.title}
                                            <small className='notification-text'>{item.subtitle}</small>
                                        </Media>
                                    </Fragment>
                                ) : (
                                    <Fragment>
                                        {item.title}
                                        {item.switch}
                                    </Fragment>
                                )}
                            </Media>
                        </Link>
                    )
                })}
            </PerfectScrollbar>
        )
    }
    /*eslint-enable */

    return (
        <UncontrolledDropdown tag='li' className='dropdown-notification nav-item mr-25'>
            <DropdownToggle tag='a' className='nav-link' href='/' onClick={e => e.preventDefault()}>
                <Bell size={21}/>
                <Badge pill color='danger' className='badge-up'>
                    {cntNotif}
                </Badge>
            </DropdownToggle>
            <DropdownMenu tag='ul' right className='dropdown-menu-media mt-0'>
                <li className='dropdown-menu-header'>
                    <DropdownItem className='d-flex' tag='div' header>
                        <h4 className='notification-title mb-0 mr-auto'>Уведомления</h4>
                        <Badge tag='div' color='light-primary' pill>
                            {cntNotif}
                        </Badge>
                    </DropdownItem>
                </li>
                {renderNotificationItems()}
                {/*{cntNotif > 0 &&
                <li className='dropdown-menu-footer'>
                    <Button.Ripple color='primary' block>
                        Отметить все как прочитано
                    </Button.Ripple>
                </li>
                }*/}
            </DropdownMenu>
        </UncontrolledDropdown>
    )
}

const mapStateToProps = (state) => ({
    email: state.profilePage.profile.email,
    ecoId: state.authPage.ecoId,
    notifications: state.appInit.notifications
});

export default withRouter(connect(mapStateToProps, {readNotification})(NotificationDropdown));
