// ** React Imports
import { Link } from 'react-router-dom'

// ** Custom Components
import Avatar from '@components/avatar'

// ** Third Party Components
import { UncontrolledDropdown, DropdownMenu, DropdownToggle, DropdownItem } from 'reactstrap'
import { User, HelpCircle, Power } from 'react-feather'
import Male from '../../../../assets/images/user/m.svg';
import FeMale from '../../../../assets/images/user/f.svg';
import React from "react";

const UserDropdown = (props) => {
  const logOut = (e) => {
    e.preventDefault();
    props.logout();
  }
  return (
    <UncontrolledDropdown tag='li' className='dropdown-user nav-item'>
      <DropdownToggle href='/' tag='a' className='nav-link dropdown-user-link' onClick={e => e.preventDefault()}>
        <div className='user-nav d-sm-flex d-none'>
          <span className='user-name font-weight-bold'>{props.login}</span>
        </div>
        <Avatar img={props.sex === 'm' ? Male : FeMale} imgHeight='40' imgWidth='40' status='online' />
      </DropdownToggle>
      <DropdownMenu right>
        <DropdownItem tag={Link} to='/pages/account-settings'>
          <User size={14} className='mr-75' />
          <span className='align-middle'>Профиль</span>
        </DropdownItem>
        <DropdownItem tag='a' href='#' onClick={(e) => logOut(e)}>
          <Power size={14} className='mr-75' />
          <span className='align-middle'>Выход</span>
        </DropdownItem>
      </DropdownMenu>
    </UncontrolledDropdown>
  )
}

export default UserDropdown;
