export const guardLinks = (items, endDate, percent) => {
    for (const key in items) {
        if (items[key].guard === 'endDate') {
            if ((items[key].guardSwitch === 1 && !endDate) || (items[key].guardSwitch === 0 && endDate)) {
                items.splice(key, 1);
            }
        } else if (items[key].guard === 'percent') {
            if ((percent < items[key].guardSwitch) || !percent) {
                items.splice(key, 1);
            }
        }
    }
    return items;
}